import request from '../../utils/request.js';
const is_page = getCurrentPages();

export default {
	data() {
		return {
			user: {},
			data: {
				s3_xi: [],
				s3_xiu: [],
				s5: []
			},
			info_data: [],
			nav: [
				{
					name: '清洗',
					id: 's3_xi',
					num: '',
					page: 1
				},
				{
					name: '修复',
					id: 's3_xiu',
					num: '',
					page: 1
				},
				{
					name: '退回',
					id: 's5',
					num: '',
					page: 1
				}
			],
			priceArr: ['10', '20', '30', '40', '50'],
			select: 's3_xi', // 顶部导航条状态
			selectIndex: 0,
			is_lookInfo: false, // 已退回页是否为查看详情
			await_return_select: 1, // 待退回筛选
			showBtn: false, //底部弹出框
			is_num: 0, //选中的数量
			all_price: 0, //选中的价格
			is_all_select: false, // 是否全选
			unable_num: 0, // 不可修复数量
			is_select_num: 0, // 已选退回数量
			price_select: 10,
			page: 1,
			count: '', //订单数量
			code: '' //已退回的code
		};
	},
	methods: {
		getCount() {
			request('/auth/getCount', 'POST', {
				consignor_id: this.user.id
			}).then(res => {
				let data = res.data;
				for (let key in data) {
					this.nav.map(item => {
						if (item.id === key) {
							item.num = data[key];
						}
					});
				}
			});
		},
		// 查看大图
		bigImg(e) {
			let { index, list } = e.currentTarget.dataset;
			uni.previewImage({
				current: list[index], // 当前显示图片的http链接
				urls: list // 需要预览的图片http链接列表
			});
		},
		// 获取数据
		getData(states, page) {
			uni.showLoading({
				title: '加载中...'
			});
			let user = JSON.parse(uni.getStorageSync('user'));
			request('/api/auth/orderList', 'POST', {
				states,
				consignor_id: user.id,
				page: this.page
			}).then(res => {
				uni.hideLoading();
				if (res.data.data.length == 0) {
					uni.showToast({
						title: '暂无数据可加载',
						icon: 'none'
					});
					this.nav[this.selectIndex].page-=1;
					return;
				}
				const s3_xi = [],
					s3_xiu = [],
					s5 = [];
				res.data.data.forEach(item => {
					item.end_image = JSON.parse(item.end_image);
					item.original_image = JSON.parse(item.original_image);
					if (item.s3_xi) {
						s3_xi.push(item);
					}
					if (item.s5_xiu) {
						s5_xiu.push(item);
					}
					if (item.states === 5) {
						s5.push(item);
					}
				});
				this.data.s3_xi = s3_xi;
				this.data.s3_xiu = s3_xiu;
				this.data.s5 = s5;
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh();
			});
		},
		// 重置页面
		resetPage() {
			this.getCount();
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		// 切换顶部nav
		change_nav(e) {
			let index = e.currentTarget.dataset.index;
			// let page = e.currentTarget.dataset.page;
			let selectIndex = e.currentTarget.dataset.selectindex;
			console.log(selectIndex, index);
			this.selectIndex = selectIndex;
			this.page = this.nav[selectIndex].page;
			if (index == this.select) {
				return;
			}
			this.select = index;
			// this.resetPage();
			switch (index) {
				case 's3_xi':
					this.getCount();
					this.getData(3, this.page);
					break;
				case 's3_xiu':
					this.getCount();
					this.getData(3, this.page);
					break;
				case 's5':
					this.getCount();
					this.getData(5, this.page);
					break;
				default:
					break;
			}
		}
	},
	onPullDownRefresh: function() {
		uni.showNavigationBarLoading();
		this.page = 1;
		this.getCount();
		if (this.select === 's3_xi' || this.select === 's3_xiu') {
			this.getData(3);
		} else {
			this.getData(5);
		}
	},
	// 页面触底
	onReachBottom() {
		let index = this.select;
		let selectIndex = this.selectIndex;
		this.nav[selectIndex].page ++;
		this.page = this.nav[selectIndex].page;
		switch (index) {
			case 's3_xi':
				this.getCount();
				this.getData(3, this.page);
				break;
			case 's3_xiu':
				this.getCount();
				this.getData(3, this.page);
				break;
			case 's5':
				this.getCount();
				this.getData(5, this.page);
				break;
			default:
				break;
		}
	},
	onLoad() {
		let user = JSON.parse(uni.getStorageSync('user'));
		this.user = user;
		this.price_select = Number(user.quota);
		this.getData(3);
		this.getCount();
	}
};
